export default {
	onLoad() {
		this.getPicCate();
		this.handleClick(0);
	},
	data() {
		return {
			cates: [],
			activeIndex: 0,
			pics: [],
			nopic: false,
		}
	},
	methods: {
		async getPicCate() {
			let res = await this.$http({
				url: '/pic-cate.json',
			});
			
			this.cates = res.data.list;
		},
		async getPic(index) {
			let res = await this.$http({
				url: `/pics-${index}.json`,
			});
			
			this.pics = res.data.list;
		} 
		,
		handleClick(index) {
			this.activeIndex = index;
			if (index === 8 || index === 9) {
				this.getPic(index);
				this.nopic = false;
			} else {
				this.pics = [];
				this.nopic = true;
			}
		},
		handleClickImg(index) {
			let picUrls = this.pics.map(item => item.img);
			uni.previewImage({
				urls: picUrls,
				current: index,
				indicator: 'number',
				loop:true,
			})
		}
	}
}
